import { Redirect, Route } from "react-router-dom";
import {
  IonApp,
  IonRouterOutlet,
  IonSplitPane,
  setupIonicReact,
} from "@ionic/react";

import { IonReactRouter } from "@ionic/react-router";
import Home from "./pages/Home";

/* Core CSS required for Ionic components to work properly */
import "@ionic/react/css/core.css";

/* Basic CSS for apps built with Ionic */
import "@ionic/react/css/normalize.css";
import "@ionic/react/css/structure.css";
import "@ionic/react/css/typography.css";

/* Optional CSS utils that can be commented out */
import "@ionic/react/css/padding.css";
import "@ionic/react/css/float-elements.css";
import "@ionic/react/css/text-alignment.css";
import "@ionic/react/css/text-transformation.css";
import "@ionic/react/css/flex-utils.css";
import "@ionic/react/css/display.css";

/* Theme variables */
import "./theme/variables.css";
import "./theme/App.css";

import Menu from "./components/Menu";
import About from "./pages/About";
import CropImage from "./pages/CropImage";
import Tweets from "./pages/Tweets";
import { useRef } from "react";

setupIonicReact();

const App: React.FC = () => {
  const routerRef = useRef<HTMLIonRouterOutletElement | null>(null);

  return (
    <IonApp className="dark">
      <IonReactRouter basename="/mocholads">
        <IonSplitPane contentId="mainContent">
          <Menu />
          <IonRouterOutlet ref={routerRef} animated={false} id="mainContent">
            <Route
              exact
              path="/home"
              render={() => <Home router={routerRef.current} />}
            />
            {/* <Route exact path="/tweets" component={Tweets} /> */}
            <Route exact path="/about" component={About} />
            {/* <Route path="/crop-image/:link" component={CropImage} /> */}
            <Redirect from="/" to="/home" exact />
            <Redirect from="/mocholads" to="/home" exact />
          </IonRouterOutlet>
        </IonSplitPane>
      </IonReactRouter>
    </IonApp>
  );
};

export default App;
