function importAll(r: __WebpackModuleApi.RequireContext) {
    return r.keys().map(key => {

        return {
            url: r(key).default,
            sex: key.substring(key.indexOf('[') + 1, key.indexOf(']'))
        }
    });
}

const Module = require.context('../assets/profiles', false, /\.(png|jpe?g|svg)$/);
const images = importAll(Module);

export default images as { url: string, sex: string }[];