import create, { SetState, GetState } from 'zustand'
import { persist, StoreApiWithPersist } from "zustand/middleware";
import memes from './memes';
import profiles from './profiles'

import { nanoid } from 'nanoid'




export type tweetsProps = {

    fullText: string,
    userId: string,
    tweetId: string,
    name: string,
    userName: string,
    profileImage: string,
    mentions: { userName: string, name: string, userId: string }[],
    shouldHideMedia?: boolean,
    hasReplay?: boolean,
    media: { url: string, hidded: boolean }[]
}

export type StateProps = {
    activeTweet: tweetsProps[];
    setState: SetState<StateProps>;
    tweets: tweetsProps[] | null,
}


