const UID = nanoid();

const useStore = create<StateProps, SetState<StateProps>, GetState<StateProps>, StoreApiWithPersist<StateProps>>(

    (set: SetState<StateProps>, get: GetState<StateProps>) => ({
        activeTweet: [{
            name: 'بینام',
            userId: 'user_' + UID,
            tweetId: '1234_' + UID,
            fullText: 'متنی بنویسید ...',
            media: [{ url: memes[Math.floor(Math.random() * memes.length)].url, hidded: false }],
            profileImage: profiles[Math.floor(Math.random() * profiles.length)].url,
            userName: '@nameless',
            shouldHideMedia: true,
            hasReplay: true,
            mentions: [{
                name: 'بینام',
                userId: 'mention_user_' + UID,
                userName: 'nameless'
            }]
        },],
        setState: set,
        tweets: null
    })

)


export default useStore;