function importAll(r: __WebpackModuleApi.RequireContext) {
    return r.keys().map(key => {

        return {
            url: r(key).default,
            tags: key.substring(key.indexOf('[') + 1, key.indexOf(']')).split(',')
        }
    });
}

const Module = require.context('../assets/memes', false, /\.(png|jpe?g|svg)$/);
const images = importAll(Module);

export default images as { url: string, tags: string[] }[];