import {
  IonPage,
  IonHeader,
  IonToolbar,
  IonButtons,
  IonMenuButton,
  IonContent,
  IonBackButton,
} from "@ionic/react";

import "./About.css";

const About: React.FC = () => {
  return (
    <IonPage>
      <IonHeader>
        <IonToolbar>
          <div className="header-neon neon">
            <IonBackButton defaultHref="home" />
            <span style={{ marginLeft: 16 }}>About</span>

            <IonButtons style={{ marginLeft: "auto" }}>
              <IonMenuButton />
            </IonButtons>
          </div>
        </IonToolbar>
      </IonHeader>
      <IonContent>
        <div className="context">
          <h2>Created By</h2>
          <h1
            onClick={() => {
              window.location.href = "https://instagram.com/arasrezaie";
            }}
          >
            arasrezaie
          </h1>
        </div>
        <div className="area">
          <ul className="circles">
            <li></li>
            <li></li>
            <li></li>
            <li></li>
            <li></li>
            <li></li>
            <li></li>
            <li></li>
            <li></li>
            <li></li>
          </ul>
        </div>
      </IonContent>
    </IonPage>
  );
};

export default About;
