import {
  IonContent,
  IonIcon,
  IonItem,
  IonLabel,
  IonList,
  IonMenu,
  IonMenuToggle,
} from "@ionic/react";

import avatar from "../assets/mocholmag.png";

import "./Menu.css";

import React from "react";
import { useLocation } from "react-router-dom";
import {
  home,
  information,
  logoInstagram,
  logoTwitter,
  search,
} from "ionicons/icons";

interface AppPage {
  url: string;
  title: string;
  icon: string;
}

const appPages: AppPage[] = [
  {
    title: "Home",
    url: "/home",
    icon: home,
  },
  // {
  //   title: "Search",
  //   url: "/search",
  //   icon: search,
  // },
  // {
  //   title: "Tweets",
  //   url: "/tweets",
  //   icon: logoTwitter,
  // },
  // {
  //   title: "InstaQ",
  //   url: "/insta-q",
  //   icon: logoInstagram,
  // },
];

const Menu: React.FC = () => {
  const location = useLocation();

  return (
    <IonMenu contentId="mainContent" type="overlay" side="end">
      <div
        style={{
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
          flexDirection: "column",
          margin: "auto",
          padding: "16px 0",
        }}
      >
        <img style={{ maxWidth: "150px" }} src={avatar} />
      </div>

      <IonContent>
        <IonList id="inbox-list">
          {appPages.map((appPage, index) => {
            return (
              <IonMenuToggle key={index} autoHide={false}>
                <IonItem
                  className={
                    location.pathname === appPage.url ? "selected" : ""
                  }
                  routerLink={appPage.url}
                  routerDirection="none"
                  lines="none"
                  detail={false}
                >
                  <IonIcon color="white" slot="start" icon={appPage.icon} />
                  <IonLabel style={{ fontWeight: 700 }}>
                    {appPage.title}
                  </IonLabel>
                </IonItem>
              </IonMenuToggle>
            );
          })}
        </IonList>
        <IonList id="inbox-list">
          <IonMenuToggle autoHide={false}>
            <IonItem
              className={location.pathname === "/about" ? "selected" : ""}
              routerLink={"/about"}
              routerDirection="none"
              lines="none"
              detail={false}
            >
              <IonIcon color="white" slot="start" icon={information} />
              <IonLabel style={{ fontWeight: 700 }}>About</IonLabel>
            </IonItem>
          </IonMenuToggle>
        </IonList>
      </IonContent>
    </IonMenu>
  );
};

export default Menu;
